
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import mainbg from '../../img/mainbg.png';
import egg from '../../img/egg.png';
import turr from '../../img/turr.png';
import turl from '../../img/tur2.png';

const MainTop=()=>{
    const [position, setPosition] = useState(0);
    const [direction, setDirection] = useState(1);
    const navigate = useNavigate();

    useEffect(() => {
        const moveTurtle = setInterval(() => {
            setPosition(prevPosition => {
                let newPosition = prevPosition + direction * 7; // Move 5px each step
                if (newPosition >= 710 || newPosition <= 0) { // 850px (width) - 70px (turtle width) = 780px max position
                    setDirection(prevDirection => -prevDirection);
                    newPosition = prevPosition; // Reset to prevent over move
                }
                return newPosition;
            });
        }, 80); // Adjust this value for speed

        return () => clearInterval(moveTurtle);
    }, [direction]);

    const handleProject = () => {
        navigate('/project');
    };
 
    
    const Styles = {
        bg: {
        backgroundImage: `url(${mainbg})`,
        backgroundSize: "cover",
        width: "840px",
        height:"302px",
        position: "relative",
        },

        button:{
            position: "absolute",
            width:"100px",
            height: "60px",
            backgroundColor: "transparent",
            border:"none",
            bottom: "45px", 
            left: "30px",
            // marginBottom: "10px",
            textAlign:"right",
            color:"black",
            fontFamily: "ONE-Mobile-POP",
        },
        egg :{
            height: "90px",
            // marginLeft: "10px",
            marginTop:"0px"
        },
        turtle:{
            position: "absolute",
            height:"70px",
            bottom: "10px", 
            right: "20px",
            left: `${position}px`,
        },
    
    }
    return(
        <div 
            style={Styles.bg}>
            <button 
                style={Styles.button}
                onClick={handleProject}>
                click !!
                <img src={egg} style={Styles.egg} alt=""></img>
            </button>
            <img src={direction === 1 ? turr : turl } alt="" style={Styles.turtle} />
        </div>
    ) 
}
export default MainTop;